<!-- components/page1/cityBar.vue -->
<template>
    <div class="cityBar"></div>
</template>

<script>
export default {
    name: 'cityBar',
    props: {
        data: Object
    },
    data() {
        return {
            myChart: null
        }
    },
    methods: {
        setChart() {
            if (!this.data || !this.data.data || this.data.data.length === 0) {
                // 如果没有数据，使用默认数据
                return
            }
            
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'none'
                    },
                    backgroundColor: '#11367a',
                    textStyle: {
                        color: '#6dd0e3',
                        fontSize: 10,
                    },
                    formatter: (params) => {
                        return params[1].name + "<br>" + "招聘数量: " + params[1].value + "个"
                    }
                },
                grid: {
                    left: '4%',
                    top: '5%',
                    bottom: 10,
                    containLabel: true,
                },
                yAxis: {
                    type: 'category',
                    inverse: true,
                    position: 'left',
                    axisLine: {
                        show: true,
                        lineStyle: {
                            color: '#1a3c58'
                        }
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: true,
                        margin: 13,
                        fontSize: 10,
                        color: '#75deef'
                    },
                    data: this.data.data || []
                },
                xAxis: {
                    type: 'value',
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: false,
                    },
                    splitLine: {
                        show: false,
                    }
                },
                series: [
                    {
                        name: '辅助',
                        type: 'bar',
                        stack: '总量',
                        barCategoryGap: 20,
                        barWidth: 8,
                        itemStyle: {
                            color: 'rgba(0,0,0,0)'
                        },
                        data: this.setData()
                    },
                    {
                        name: this.data.name || '城市分布',
                        type: 'bar',
                        stack: '总量',
                        barCategoryGap: 20,
                        barWidth: 8,
                        itemStyle: {
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 1,
                                y2: 0,
                                colorStops: [{
                                    offset: 0, color: 'rgb(' + (this.data.color || '44,168,254') + ',0.1)'
                                }, {
                                    offset: 1, color: 'rgb(' + (this.data.color || '44,168,254') + ',1)'
                                }],
                                globalCoord: false
                            },
                            borderRadius: 5,
                        },
                        data: this.data.value || []
                    },
                ]
            };
            
            if (!this.myChart) this.myChart = this.$echarts(this.$el);
            this.myChart.clear();
            this.myChart.resize();
            this.myChart.setOption(option);
        },
        
        setData() {
            if (!this.data || !this.data.value) return []
            
            var max = Math.max.apply(null, this.data.value);
            var arr = []
            for (let i = 0; i < this.data.value.length; i++) {
                arr.push(max / 10)
            }
            return arr;
        }
    },
    watch: {
        data: {
            handler() {
                this.setChart()
            },
            deep: true
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.setChart()
        })
    },
}
</script>

<style lang="less" scoped>
.cityBar {
    width: 100%;
    height: 100%;
}
</style>